html {
    height: 100%;
}

body {
    background-color: #282c34;
    background-image: url(/sarahklein/static/media/background.f4335e70.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0;
    height: 100%;
}

.footer {
    background-color: black;
    color: white;
    padding: 5px;
}

table {
    width: 100%;
}

td {
   text-align: center;
   width: 50%;
}

a.active {
    color: black !important;
    background-color: rgb(235, 131, 40);
}
.book {
    width: 200px;
    height: 160px;
    float: left;
    position: relative;
}

.bookImage {
    height: 150px;
}

.bookInfo {
    display: none;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0,0,0,.7);
    color: white;
}

.bookInfo > p {
    font-size: 1rem;
    line-height: 1.1rem;
}

.book:hover > .bookInfo {
    display: block;
    overflow-y: hidden;
    border-radius: 32px 32px 0 0;
}

.dateRead {
    font-style: italic;
}


img {
    display: inline-block;
    margin: 0 20px 0 20px;
}

.caption {
    height: 36px;
}

.caption span {
    border-bottom: 1px solid black
}

.image {
    display: inline-block;
}

table {
    table-layout:fixed;    
}

.tableContainer {
    width:400px;
    overflow:auto;
}

.booksContainer {
    width:40000px;
}

.row {
    text-align: center;
}

h1, h2, p, ul, li {
    font-family: sans-serif;
    font-size: 1.4rem;
    line-height: 2rem;
}
h1 {
    color: white;
}
ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
}
ul.header {
    background-color: #111;
    padding: 0;
}
ul.header li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
}
a {
    color: rgb(235, 131, 40);
}
a:visited {
    color: rgb(215, 111, 20);
}
.content {
    background-color: #FFF;
    padding: 20px 100px;
}
.content h2 {
    padding: 0;
    margin: 0;
}
.content li {
    margin-bottom: 10px;
}

